<script>
export default {
    name: "parityCar",
    data() {
        return {
            tableData: [{
                img:'',
                mileage: '99,999',
                date: '2019/10/1',
                color: '白色',
                drive: '5門5座',
                fuel: '汽油',
                drive: '2WD',
            },{
                img:'',
                mileage: '99,999',
                date: '2019/10/1',
                color: '白色',
                drive: '5門5座',
                fuel: '汽油',
                drive: '2WD',
            },{
                img:'',
                mileage: '99,999',
                date: '2019/10/1',
