<style lang='scss' scoped>
.parityCar {
    width: calc(100vw - 495px);
    margin: 100px auto;
    .search-result-card{
            display: flex;
            padding: 30px 0;
            &:nth-last-child(2) {
                border-bottom:none
            }
            .card-img {
                position: relative;
                cursor: pointer;
                img {
                    width: 200px;
                    height: 220px;
                }
            }
            .card-info {
                margin-left: 20px;
                
                .card-tag {
                    color: #fff;
                    background: #3cb07b;
                    line-height: 40px;
                    border-radius: 50px;
                    text-align: center;
                    padding: 0 30px 0 45px;
                    position: relative;
                    font-weight: bold;
                    max-width: 90px;
                    i{
                        background: #148451;
                        border-radius: 50px;
                        line-height: 30px;
                        width: 30px;
                        transform: rotate(45deg);
                        position: absolute;
                        top: 5px;
                        left: 5px;
                    }
                }
                .card-title {
                    font-size: 22px;
                    font-weight: bold;
                    margin: 30px 0 15px 0;
                    text-decoration: underline;
                }
                .card-txt {
                    max-width: 400px;
                    line-height: 25px;
                }
                .card-money {
                    background: #ef6d38;
                    line-height: 50px;
                    color: #fff;
                    font-size: 16px;
                    display: flex;
                    margin: 10px 0;
                    max-width: 200px;
                    img {
                        background: #ca582a;
                        padding:10px;
                    }
                    p {
                        padding-left: 10px;
                    }
                }
                .card-collect {
                    display: flex;
                    div {
                        margin: 10px;
                        padding: 0 10px;
                        transition: all .3s;
                        cursor: pointer;
                        i {
                            margin-right: 10px;
                        }
                        &:hover {
                            color: #ef6d38;
                        }
                    }
                    .card-collect-active {
                        color: #ef6d38;
                    }
                    span {
                        width: 1px;
                        display: block;
                        background: #dddddd;
                    }
                }
            }
            @media screen and (max-width: 768px){  
                align-items: center;
                flex-direction: column;
                .card-info {
                    margin: 20px 0;
                    .card-title {
                        text-align: center;
                    }
                    .card-money {
                        margin: 30px auto;
                    }
                    .card-collect {
                        margin: auto;
                        right: auto;
                    }
                }
                .card-tag {
                        margin: auto;
                    }
            }
    }
    @media screen and (max-width: 768px){  
        width: auto;
    }
}

</style>


